  }
}
@media (max-width: 767px) {
  .catalog__item {
    padding: 35px 25px;
  }
}
.catalog__item div {
  height: 100%;
}
.catalog__item h3 {
  margin: 25px 0 0;
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
  z-index: 3;
}
.catalog__item img {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  object-fit: cover;
  object-position: center;
  overflow: hidden;
  background-color: #011c40;
  z-index: 2;
}
.catalog__item b {
  margin-bottom: 10px;
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
}
.catalog__item p {
  margin: 0 0 20px;
  font-size: 14px;
  line-height: 20px;
  color: #d8d8d9;
}
.catalog__item a {
  display: block;
}
.catalog__item:hover, .catalog__item:focus, .catalog__item:focus-within {
  flex-direction: column;
}
.catalog__item:hover h3, .catalog__item:focus h3, .catalog__item:focus-within h3 {
  margin: 0 0 25px;
}
.catalog__item:hover img, .catalog__item:focus img, .catalog__item:focus-within img {
  display: none;
}

.booking {
  position: relative;
  padding: 50px 0;
}
@media (max-width: 1023px) {
  .booking {
    padding: 100px 0;
  }
}
@media (max-width: 767px) {
  .booking {
    padding: 65px 0;
  }
}

.booking__wrapper {
  display: grid;
  grid-template-columns: 1fr 403px;
  align-items: center;
  padding: 0 60px;
}
@media (max-width: 1023px) {
  .booking__wrapper {
    display: flex;
    flex-direction: column;
    padding: 0;
  }
}

.booking__title {
  margin: 0 0 30px;
  font-weight: 700;
  font-size: 40px;
  line-height: 60px;
}
@media (max-width: 1023px) {
  .booking__title {
    margin-bottom: 25px;
    font-size: 34px;
    line-height: 51px;
    text-align: center;
  }
}
@media (max-width: 767px) {
  .booking__title {
    font-size: 24px;
    line-height: 31px;
  }
}

.booking__text {
  margin: 0;
  max-width: 360px;
  font-size: 18px;
  line-height: 27px;
}
.booking__text br {
  display: none;
}
@media (max-width: 1023px) {
  .booking__text {
    margin: 0 auto 60px;
    text-align: center;
  }
}
@media (max-width: 767px) {
  .booking__text {
    margin-bottom: 40px;
    font-size: 16px;
    line-height: 22px;
  }
  .booking__text br {
    display: block;
  }
}

.booking__bg {
  position: absolute;
  width: 100vw;
  min-width: 320px;
  height: 100%;
  left: 0;
  top: 0;
  object-fit: cover;
  object-position: center;
  overflow: hidden;
  background-color: #e3ecf6;
  z-index: -1;
}

.booking__form {
  display: flex;
  flex-direction: column;
  max-width: 403px;
  padding: 50px;
  color: #f9fbfd;
  background-color: #011c40;
}
@media (max-width: 767px) {
  .booking__form {
    padding: 40px 30px;
  }
}
.booking__form p {
  margin: 0 0 35px;
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
}
@media (max-width: 767px) {
  .booking__form p {
    line-height: 25px;
    text-align: center;
  }
}

.booking__field input {
  width: 100%;
  margin-bottom: 20px;
  padding: 11px 16px;
  font-size: 14px;
  line-height: 21px;
  border: none;
}
.booking__field input:disabled ~ span {
  opacity: 0.5;
  cursor: default;
}

.booking__control {
  position: relative;
  margin-bottom: 30px;
}
.booking__control:focus-within label {
  outline: #f9fbfd auto 1px;
  outline-offset: 5px;
}
.booking__control input:checked + span::after {
  content: "";
  position: absolute;
  top: 2px;
  left: 5px;
  width: 7px;
  height: 9px;
  border-right: 1px solid #101a56;
  border-bottom: 1px solid #101a56;
  transform: rotate(45deg);
}
.booking__control input:disabled ~ span {
  opacity: 0.5;
  cursor: default;
}
.booking__control input:hover ~ span {
  opacity: 0.8;
}
.booking__control label {
  display: flex;
  align-items: center;
  cursor: pointer;
}
.booking__control input + span {
  position: relative;
  display: block;
  width: 18px;
  height: 18px;
  flex-shrink: 0;
  box-sizing: border-box;
  margin-right: 5px;
  background-color: #f9fbfd;
}
.booking__control span {
  margin: 0;
  font-size: 10px;
  line-height: 13px;
  color: #f9fbfd;
}
@media (max-width: 767px) {
  .booking__control span {
    line-height: 12px;
  }
}

.footer {
